*{
  background-color: lightcyan
}
.App-title {
  text-align: center;
}
.income1{
  grid-area: income; 
}
.expense1{
  grid-area: expense;
}
.balance{
  grid-area: balance;
  margin-top: 100px;
}
.Main1{
  display: grid;
  grid-template-areas: 
  'income expense'
  'balance balance';
}
.Main{
  border: 1px solid black;
  width: 1000px;
  height: 500px;
  margin-top: 40px;
  margin-left: 350px;
}
h2{
  margin-left: 100px;
  text-decoration: underline;
}
h3{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
span{
  padding: 10px;
  line-height: 25px;
}
.input1{
  width: 250px;
  height: 30px;
  margin-right: 20px;  
}
.input2{
  width: 150px;
  height: 30px;
}
select{
  height: 36px;
  border-radius: 10px;
  margin-left: 20px;
}
.Btn{
  height: 36px;
  width: 50px;
  margin-left: 20px;
}
ul{
  list-style-type: upper-roman;
}


